//-------------------flex----------------------------------//

%flex-1 {
}

.flex {
  @include breakpointMaker(true) {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;

    &-inline {
      display: -ms-inline-flexbox;
      display: inline-flex;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-between;
    }
    &-col {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      flex-direction: column;
      justify-content: space-between;

      &-inline {
        display: -ms-inline-flexbox;
        display: inline-flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: space-between;
      }
    }
  }
}
.stretch > * {
  flex: 1;
}

//-------------------childs----------------------------------//
.flex-auto {
  @include breakpointMaker {
    flex-basis: 100%;
    flex: 1;
  }
}

